.men-women-collection {
    width: 85vw;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    gap: 2rem;
    font-family: 'Courier Prime', monospace;
    font-size: 0.8rem;
    height: 3vh;
}

.products-catalog {
    width: 85vw;
    margin: 0 auto;
}

.products-catalog-wrapper {
    display: grid;
    grid-template-columns: 49% 49%;
    grid-template-rows: 25% 25% 25% 25%;
    justify-content: space-between;
}

.product-container {
    height: auto;
    width: auto;
}

.product-image-wrapper {
    height: 60%;
    width: 100%;  
}

.product-image {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.product-description {
    padding-top: 0.2rem;
    line-height: 1.3rem;
    height: 40%;
    width: 100%;  
}

.product-brand {
    font-weight: bold;
}

.product-type {
    font-family: 'Courier Prime', monospace;
    font-size: 0.8rem;
}

.more-coming-soon {
    display: flex;
    justify-content: center;
    font-family: 'Courier Prime', monospace;
}

@media only screen and (min-width: 475px) {
    .products-catalog-wrapper {
        grid-template-columns: 33% 33% 33%;
        grid-template-rows: 33% 33% 33% 33%;
    }
    .product-type {
        font-family: 'Courier Prime', monospace;
        font-size: 0.7rem;
    }    
}

@media only screen and (min-width: 650px) {
    .product-type {
        font-family: 'Courier Prime', monospace;
        font-size: 0.8rem;
    }    
}

@media only screen and (min-width: 770px) {
    .sort-by {
        width: 90vw;
    }
    .products-catalog {
        width: 90vw;
        margin: 0 auto;
    }
}

@media only screen and (min-width: 850px) {
    .product-type {
        font-family: 'Courier Prime', monospace;
        font-size: 0.9rem;
    }    
}

@media only screen and (min-width: 900px) {
    .sort-by {
        width: 800px;
    }
    .products-catalog-wrapper {
        width: 800px;
        grid-template-columns: 33% 33% 33%;
        grid-template-rows: 33% 33% 33% 33%;
        margin: 0 auto;
    }
}

@media only screen and (min-width: 1000px) {
    .sort-by {
        width: 900px;
    }
    .products-catalog-wrapper {
        width: 900px;
        grid-template-columns: 33% 33% 33%;
        grid-template-rows: 33% 33% 33% 33%;
        margin: 0 auto;
    }
}
